<template>
  <div>
<!--   <el-container class="header">-->
<!--      <el-header>-->
<!--        <span> {{courseName}}</span>-->
<!--        <el-button class="exit" v-on:click="goToHelloWorld"></el-button>-->
<!--      </el-header>-->
<!--    </el-container>-->
    <el-container class="main">
      <el-aside width="show?'64px':'400px'">
        <StudentNav></StudentNav>
      </el-aside>
      <el-container>
        <el-header>
          <StudentHeading></StudentHeading>
        </el-header>
        <el-main>
          <el-row> {{courseName}}</el-row>
        <el-row class="buttons">
          <el-button v-on:click="commentCourse" type="primary" size="small" ></el-button>
          <el-button v-on:click="returnStudentAllComment" type="primary" size="small"></el-button>
        </el-row>
        <el-row class="buttons">
          <el-col :span="20">
            <el-input class="input" v-model="contentInput" type="textarea" :rows="2" placeholder="">
            </el-input>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <div v-for="(comment) in commentList" v-bind:key="comment">
          <el-row class="time">
            {{comment.time}}
          </el-row>
          <el-row class="userName">
            {{comment.userNickName}}({{comment.userName}}) :
          </el-row>
          <el-row class="content">
            {{comment.content}}
          </el-row>
          <el-row class="delete">
            <div v-if="comment.userName === userName">
              <el-link type="danger" v-on:click="deleteComment(comment.id)"></el-link>
            </div>
          </el-row>
          <el-divider></el-divider>
        </div>
      </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<style scoped>
  .buttons {
    margin-bottom: 10px;
  }
  .input {
    font-size: large;
  }
  .time {
    font-size: small;
    color: #e2e2e2;
  }
  .userName {
    font-size: medium;
    color: #66b1ff;
  }
  .content {
    font-size: medium;
  }
</style>

<script>
import StudentNav from '../StudentNav'
import StudentHeading from '../StudentHeading'
export default {
  name: 'StudentComment',
  components: {StudentNav, StudentHeading},
  data: function () {
    return {
      userName: '',
      userNickName: '',
      courseId: 'id',
      courseName: '',
      contentInput: '',
      time: '',
      commentList: [{
        id: 1,
        userName: '1',
        userNickName: '1',
        content: '1',
        time: '2021-11-19 11:11:11'
      }, {
        id: 2,
        userName: 'admin',
        userNickName: '2',
        content: '2',
        time: '2021-11-19 11:11:11'
      }, {
        id: 3,
        userName: '3',
        userNickName: '3',
        content: '3',
        time: '2021-11-19 11:11:11'
      }, {
        id: 4,
        userName: '1',
        userNickName: '1',
        content: '1',
        time: '2021-11-19 11:11:11'
      }, {
        id: 5,
        userName: '2',
        userNickName: '2',
        content: '2',
        time: '2021-11-19 11:11:11'
      }, {
        id: 6,
        userName: '3',
        userNickName: '3',
        content: '3',
        time: '2021-11-19 11:11:11'
      }
      ]
    }
  },
  mounted () {
    this.userName = this.cookie.getCookie('userName')
    this.userNickName = this.cookie.getCookie('userNickName')
    this.courseId = this.$route.query.courseId
    this.courseName = this.$route.query.courseName
    this.getCommentList()
  },
  methods: {
    getTime: function () {
      let dt = new Date()
      let yyyy = dt.getFullYear()
      let MM = (dt.getMonth() + 1).toString().padStart(2, '0')
      let dd = dt.getDate().toString().padStart(2, '0')
      let h = dt.getHours().toString().padStart(2, '0')
      let m = dt.getMinutes().toString().padStart(2, '0')
      let s = dt.getSeconds().toString().padStart(2, '0')
      this.time = yyyy + '-' + MM + '-' + dd + ' ' + h + ':' + m + ':' + s
    },
    getCommentList: function () {
      let that = this
      this.$http.request({
        url: that.$url + 'GetCommentList/',
        method: 'get',
        params: {
          courseId: that.courseId
        }
      }).then(function (response) {
        console.log(response.data)
        that.commentList = response.data
      }).catch(function (error) {
        console.log(error)
      })
    },
    commentCourse: function () {
      let that = this
      that.getTime()
      this.$http.request({
        url: that.$url + 'CommentCourse/',
        method: 'get',
        params: {
          courseId: that.courseId,
          userName: that.userName,
          userNickName: that.userNickName,
          content: that.contentInput,
          time: that.time
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data === 0) {
          that.$message.success('')
          that.getCommentList()
          that.contentInput = ''
        } else {
          that.$message.error('')
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    deleteComment: function (commentId) {
      let that = this
      that.getTime()
      this.$http.request({
        url: that.$url + 'DeleteComment/',
        method: 'get',
        params: {
          commentId: commentId
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data === 0) {
          that.$message.success('')
          that.getCommentList()
        } else {
          that.$message.error('')
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    returnStudentAllComment: function () {
      let that = this
      that.$router.push({
        name: 'StudentAllComment'
      })
    },
    goToHelloWorld: function () {
      this.cookie.clearCookie('userName')
      this.cookie.clearCookie('userNickName')
      this.$router.replace('/')
    }
  }
}
</script>

<style scope>
  @import "../../../assets/css/head.css";
  @import "../../../assets/css/Nav.css";
</style>



